.Button {
    background-color: rgba(255, 59, 48,0.6);
    border-radius: 25px;
    padding: 8px 20px;
    border: none;
    color: black;
    outline-style: none;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    position: absolute;
    top: 10px;
    right: 10px;
}